@keyframes background-rotate {
  0% {
    transform: rotate(0deg) scale(1);
  }
  25% {
    transform: rotate(90deg) scale(1.3);
  }
  50% {
    transform: rotate(180deg) scale(1);
  }
  75% {
    transform: rotate(270deg) scale(1.3);
  }
  100% {
    transform: rotate(360deg) scale(1);
  }
}
@keyframes background-scale {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1);
  }
  75% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
.background {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden;
  z-index: 0;
  animation: background-scale 50s linear 0s infinite;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 71.89%);
}
.background .spinner {
  position: absolute;
  top: -50%;
  left: -10%;
  right: -10%;
  bottom: -50%;
  background: url(../../assets/img/webp/background.webp) 0 0;
  background-size: contain;
  background-position: center;
  animation: background-rotate 550s linear 0s infinite;
}

@media (orientation: portrait) {
  .background .spinner {
    top: -10%;
    left: -50%;
    right: -50%;
    bottom: -10%;
  }
}
