.aside-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  padding: 125px 40px 125px 120px;
  width: 425px;
  z-index: 500;
  background: black;
  border-right-width: 1px;
  border-right-style: solid;
  border-left-width: 0;
  border-left-style: none;
  border-image: linear-gradient(to bottom, #fff, rgba(0, 0, 0, 0)) 1 100%;
  transition: all 0.3s ease-in-out;
  visibility: hidden;
  opacity: 0;
  overflow: auto;
}
.aside-menu .title {
  font-size: 16px;
  font-weight: 700;
}
.aside-menu .header-menu {
  display: flex;
  align-items: center;
  margin-bottom: 50px;
}
.aside-menu .header-menu .close-aside-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  background-color: #fff;
  border-radius: 50%;
  transform: rotate(45deg);
  margin-right: 15px;
}
.aside-menu .header-menu .close-aside-menu::after, .aside-menu .header-menu .close-aside-menu::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 2px;
  background-color: #000;
}
.aside-menu .header-menu .close-aside-menu::after {
  transform: rotate(90deg);
}
.aside-menu .header-menu .link-group {
  display: flex;
  align-items: center;
}
.aside-menu .header-menu .link-group .icon {
  display: flex;
  margin-right: 15px;
}
.aside-menu .header-menu .link-group .icon:last-child {
  margin-right: 0;
}
.aside-menu .header-menu .link-group .link {
  opacity: 0.5;
  transition: all 0.3s ease-in-out;
}
.aside-menu .header-menu .link-group .link:hover {
  opacity: 1;
}
.aside-menu form {
  position: relative;
  margin-bottom: 50px;
}
.aside-menu form .form-title {
  margin-bottom: 35px;
}
.aside-menu form .input-group {
  margin-bottom: 30px;
}
.aside-menu form .form-control {
  margin-bottom: 10px;
  display: block;
  width: 100%;
  height: 30px;
  background-color: transparent;
  border-top-width: 0;
  border-top-style: none;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-image: linear-gradient(to right, #fff, rgba(0, 0, 0, 0)) 100% 1;
  outline: none;
  color: #fff;
}
.aside-menu form .form-control:last-child {
  margin-bottom: 0;
}
.aside-menu form button[type=submit] {
  border: 1px solid #fff;
  width: 100%;
  height: 40px;
  transition: all 0.3s ease-in-out;
}
.aside-menu form button[type=submit]:hover {
  background-color: #fff;
  color: #000;
}
.aside-menu form .message {
  top: 100%;
  text-align: center;
  display: none;
}
.aside-menu form .message.show {
