body {
  padding: 0;
  margin: 0;
  color: #fff;
  font-family: "Open Sans";
  font-size: 12px;
  line-height: 1.5;
  position: relative;
  background-color: #000;
}

.page-wrapper {
  position: relative;
  z-index: 2;
  overflow: hidden;
}

main.priority {
  position: relative;
  z-index: 10;
}

.container-fluid {
  padding-left: 120px;
  padding-right: 120px;
}

*, :after, :before {
  box-sizing: border-box;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

button {
  padding: 0;
  outline: none;
  border: none;
  color: #fff;
  background-color: transparent;
  cursor: pointer;
  font-family: inherit;
}

* {
  outline: none;
}

a {
  color: #fff;
}

span {
  display: inline-block;
}

.panel {
  position: relative;
}

object {
  pointer-events: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.h1 {
  font-size: 24px;
  font-weight: 700;
}

.h2 {
  font-size: 18px;
  font-weight: 700;
}

.h3 {
  font-size: 14px;
  font-weight: 700;
}

p, .description {
  margin: 0;
  line-height: 1.8;
}

.custom-row {
  margin-left: -20px;
  margin-right: -20px;
}

.custom-col {
  padding-left: 20px;
  padding-right: 20px;
}

.row {
  --bs-gutter-x: 20px;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

.w-100 {
  width: 100%;
}

.background-color-block {
  position: absolute;
  left: 0;
  top: 790px;
  right: 0;
  bottom: 0;
  background-color: #000;
  z-index: 1;
}
.background-color-block::before {
  content: "";
  position: absolute;
  top: -790px;
  height: 790px;
  left: 0;
  right: 0;
  background: linear-gradient(359.44deg, #000000 1.59%, rgba(0, 0, 0, 0) 28.72%);
}

.button-back {
  font-size: 16px;
  text-decoration: underline;
  text-transform: lowercase;
  font-weight: 700;
}

.button-border {
  border-radius: 0px 10px;
}

.panel {
  transition: all 0.3s ease-in-out;
}

.window-height {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 266px);
  margin-left: -120px;
  margin-right: -120px;
  padding-left: 120px;
  padding-right: 120px;
}

.page-wrapper.full-screen {
  display: flex;
  flex-direction: column;
  flex: 1;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: hidden;
}
.page-wrapper.full-screen .container-fluid {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.page-wrapper.full-screen .panel {
  display: flex;
  flex-direction: column;
  flex: 1;
  justify-content: space-between;
}
.page-wrapper.full-screen .panel main {
  margin-right: 40px;
  padding-bottom: 80px;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

@media (max-width: 1199px) {
  .page-wrapper.full-screen .panel main {
    margin-right: 0;
  }

  .window-height {
    min-height: calc(100vh - 206px);
    margin-left: -40px;
    margin-right: -40px;
    padding-left: 40px;
    padding-right: 40px;
  }

  .container-fluid {
    padding-left: 40px;
    padding-right: 40px;
  }

  .page-wrapper.full-screen {
    overflow-y: auto;
  }

